'use client';

import { useState } from 'react';
import { supabase } from '@/lib/supabaseClient';

type Props = {
    isOpen: boolean;
    onClose: () => void;
};

export default function AuthModal({ isOpen, onClose }: Props) {
    const [email, setEmail] = useState('');
    const [isSent, setIsSent] = useState(false);
    const [loading, setLoading] = useState(false);

    if (!isOpen) return null;

    // „É°„Éº„É´Ôºà„Éû„Ç∏„ÉÉ„ÇØ„É™„É≥„ÇØÔºâ„É≠„Ç∞„Ç§„É≥
    const handleEmailLogin = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        // ÁèæÂú®„ÅÆURLÔºà„É™„ÉÄ„Ç§„É¨„ÇØ„ÉàÂÖàÔºâ
        const redirectTo = typeof window !== 'undefined' ? window.location.origin : undefined;

        const { error } = await supabase.auth.signInWithOtp({
            email,
            options: {
                emailRedirectTo: redirectTo
            }
        });

        setLoading(false);
        if (error) {
            alert('„Ç®„É©„Éº: ' + error.message);
        } else {
            setIsSent(true);
        }
    };

    // Google„É≠„Ç∞„Ç§„É≥
    const handleGoogleLogin = async () => {
        setLoading(true);
        const redirectTo = typeof window !== 'undefined' ? window.location.origin : undefined;

        const { error } = await supabase.auth.signInWithOAuth({
            provider: 'google',
            options: {
                redirectTo
            }
        });

        if (error) {
            setLoading(false);
            alert('„Ç®„É©„Éº: ' + error.message);
        }
    };

    return (
        <div
            style={{
                position: 'fixed',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                background: 'rgba(0,0,0,0.6)',
                zIndex: 3000,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
            }}
        >
            <div
                style={{
                    background: 'white',
                    padding: '30px',
                    borderRadius: '12px',
                    width: '90%',
                    maxWidth: '350px',
                    position: 'relative'
                }}
            >
                {/* Èñâ„Åò„Çã„Éú„Çø„É≥ */}
                <button
                    onClick={onClose}
                    style={{
                        position: 'absolute',
                        top: '10px',
                        right: '10px',
                        background: 'none',
                        border: 'none',
                        fontSize: '20px',
                        cursor: 'pointer'
                    }}
                >
                    √ó
                </button>

                <h2 style={{ marginTop: 0, textAlign: 'center' }}>„É≠„Ç∞„Ç§„É≥ / ÁôªÈå≤</h2>
                <p style={{ fontSize: '13px', color: '#666', marginBottom: '20px', textAlign: 'center' }}>
                    „É≠„Ç∞„Ç§„É≥„Åô„Çã„Å®„ÄÅËá™ÂàÜ„ÅÆÊäïÁ®øÂ±•Ê≠¥„ÅÆÁ¢∫Ë™ç„ÇÑÂâäÈô§„Åå„Åß„Åç„Çã„Çà„ÅÜ„Å´„Å™„Çä„Åæ„Åô„ÄÇ
                </p>

                {isSent ? (
                    <div style={{ textAlign: 'center', color: 'green' }}>
                        <p>Á¢∫Ë™ç„É°„Éº„É´„ÇíÈÄÅ‰ø°„Åó„Åæ„Åó„ÅüÔºÅ</p>
                        <p style={{ fontSize: '12px', color: '#333' }}>„É°„Éº„É´ÂÜÖ„ÅÆ„É™„É≥„ÇØ„Çí„ÇØ„É™„ÉÉ„ÇØ„Åó„Å¶„É≠„Ç∞„Ç§„É≥„ÇíÂÆå‰∫Ü„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ</p>
                        <button onClick={onClose} style={{ marginTop: '10px', padding: '8px 16px' }}>
                            Èñâ„Åò„Çã
                        </button>
                    </div>
                ) : (
                    <>
                        {/* Google Login */}
                        <button
                            onClick={handleGoogleLogin}
                            disabled={loading}
                            style={{
                                width: '100%',
                                padding: '10px',
                                marginBottom: '15px',
                                background: '#4285F4',
                                color: 'white',
                                border: 'none',
                                borderRadius: '4px',
                                cursor: 'pointer',
                                fontWeight: 'bold'
                            }}
                        >
                            Google„Åß„É≠„Ç∞„Ç§„É≥
                        </button>

                        <div style={{ textAlign: 'center', margin: '15px 0', fontSize: '12px', color: '#999' }}>„Åæ„Åü„ÅØ„É°„Éº„É´„Åß</div>

                        {/* Email Login */}
                        <form onSubmit={handleEmailLogin}>
                            <input
                                type="email"
                                placeholder="„É°„Éº„É´„Ç¢„Éâ„É¨„Çπ"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                required
                                style={{
                                    width: '100%',
                                    padding: '10px',
                                    marginBottom: '10px',
                                    border: '1px solid #ccc',
                                    borderRadius: '4px',
                                    boxSizing: 'border-box'
                                }}
                            />
                            <button
                                type="submit"
                                disabled={loading}
                                style={{
                                    width: '100%',
                                    padding: '10px',
                                    background: '#333',
                                    color: 'white',
                                    border: 'none',
                                    borderRadius: '4px',
                                    cursor: 'pointer'
                                }}
                            >
                                {loading ? 'ÈÄÅ‰ø°‰∏≠...' : '„É≠„Ç∞„Ç§„É≥„É™„É≥„ÇØ„ÇíÈÄÅ‰ø°'}
                            </button>
                        </form>
                    </>
                )}
            </div>
        </div>
    );
}
