'use client';

import { useState, useEffect } from 'react';
import { supabase } from '@/lib/supabaseClient';
import dynamic from 'next/dynamic';
import AuthModal from '@/components/AuthModal'; // ËøΩÂä†
import { User } from '@supabase/supabase-js';

const HazardMap = dynamic(() => import('@/components/HazardMap'), {
    loading: () => <div className="p-10 text-center">Âú∞Âõ≥„ÇíË™≠„ÅøËæº„Åø‰∏≠...</div>,
    ssr: false
});

const STORAGE_KEY = 'hazard-map-pos';

export default function Home() {
    const [posts, setPosts] = useState<any[]>([]);
    const [center, setCenter] = useState({ lat: 35.9251, lng: 139.4858 });
    const [zoom, setZoom] = useState(14);

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [isAuthModalOpen, setIsAuthModalOpen] = useState(false); // AuthÁî®

    const [formReason, setFormReason] = useState('Êöó„Åè„Å¶ÊÄñ„ÅÑ');
    const [formTime, setFormTime] = useState('night');
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [isLoadingGPS, setIsLoadingGPS] = useState(false);

    // „É¶„Éº„Ç∂„ÉºÁä∂ÊÖã
    const [user, setUser] = useState<User | null>(null);

    // 1. „É¶„Éº„Ç∂„ÉºÁõ£Ë¶ñ & Ëá™ÂãïÂåøÂêç„É≠„Ç∞„Ç§„É≥
    useEffect(() => {
        // ÁèæÂú®„ÅÆ„Çª„ÉÉ„Ç∑„Éß„É≥„ÇíÂèñÂæó
        supabase.auth.getSession().then(({ data: { session } }) => {
            if (session?.user) {
                setUser(session.user);
            } else {
                // „Çª„ÉÉ„Ç∑„Éß„É≥„Åå„Å™„ÅÑÂ†¥Âêà„ÄÅÂåøÂêç„É≠„Ç∞„Ç§„É≥„ÇíÂÆüË°å
                signInAnonymously();
            }
        });

        // „É≠„Ç∞„Ç§„É≥Áä∂ÊÖã„ÅÆÂ§âÂåñ„ÇíÁõ£Ë¶ñ
        const {
            data: { subscription }
        } = supabase.auth.onAuthStateChange((_event, session) => {
            setUser(session?.user ?? null);
        });

        return () => subscription.unsubscribe();
    }, []);

    // ÂåøÂêç„É≠„Ç∞„Ç§„É≥Âá¶ÁêÜ
    const signInAnonymously = async () => {
        const { data, error } = await supabase.auth.signInAnonymously();
        if (error) console.error('Anonymous auth error:', error);
    };

    // 2. ‰ΩçÁΩÆÊÉÖÂ†±„ÅÆÂæ©ÂÖÉÔºàÂâçÂõû„Å®Âêå„ÅòÔºâ
    useEffect(() => {
        const savedPos = localStorage.getItem(STORAGE_KEY);
        if (savedPos) {
            try {
                const parsed = JSON.parse(savedPos);
                if (parsed.lat && parsed.lng && parsed.zoom) {
                    setCenter({ lat: parsed.lat, lng: parsed.lng });
                    setZoom(parsed.zoom);
                }
            } catch (e) {
                console.error(e);
            }
        }
    }, []);

    const fetchPosts = async () => {
        const { data, error } = await supabase.from('hazard_posts').select('*').order('created_at', { ascending: false });

        if (error) console.error('Error:', error);
        else setPosts(data || []);
    };

    useEffect(() => {
        fetchPosts();
    }, []);

    const handleMapChange = (lat: number, lng: number, newZoom: number) => {
        setCenter({ lat, lng });
        setZoom(newZoom);
        localStorage.setItem(STORAGE_KEY, JSON.stringify({ lat, lng, zoom: newZoom }));
    };

    const handleCurrentLocation = () => {
        if (!navigator.geolocation) {
            alert('„Åä‰Ωø„ÅÑ„ÅÆ„Éñ„É©„Ç¶„Ç∂„Åß„ÅØÁèæÂú®Âú∞Ê©üËÉΩ„Åå‰Ωø„Åà„Åæ„Åõ„Çì');
            return;
        }
        setIsLoadingGPS(true);
        navigator.geolocation.getCurrentPosition(
            (position) => {
                const { latitude, longitude } = position.coords;
                setCenter({ lat: latitude, lng: longitude });
                setZoom(16);
                setIsLoadingGPS(false);
                localStorage.setItem(STORAGE_KEY, JSON.stringify({ lat: latitude, lng: longitude, zoom: 16 }));
            },
            (error) => {
                console.error(error);
                alert('ÁèæÂú®Âú∞„ÇíÂèñÂæó„Åß„Åç„Åæ„Åõ„Çì„Åß„Åó„Åü„ÄÇ');
                setIsLoadingGPS(false);
            }
        );
    };

    const handlePost = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!user) {
            alert('Ë™çË®ºÊÉÖÂ†±„ÅÆÂèñÂæó‰∏≠„Åß„Åô„ÄÇÂ∞ë„ÄÖ„ÅäÂæÖ„Å°„Åè„Å†„Åï„ÅÑ„ÄÇ');
            return;
        }

        setIsSubmitting(true);
        const { error } = await supabase.from('hazard_posts').insert([
            {
                lat: center.lat,
                lng: center.lng,
                reason: formReason,
                time_slot: formTime,
                // user_id„ÅØ Supabase„ÅÆRLS„Åß auth.uid() „ÅåËá™ÂãïÂèÇÁÖß„Åï„Çå„Çã„Åü„ÇÅ
                // „ÇØ„É©„Ç§„Ç¢„É≥„Éà„Åã„ÇâÈÄÅ„Çâ„Å™„Åè„Å¶„ÇÇOK„Åß„Åô„Åå„ÄÅÊòéÁ§∫ÁöÑ„Å´ÈÄÅ„Çã„Åì„Å®„ÇÇÂèØËÉΩ„Åß„Åô„ÄÇ
                // ‰ªäÂõû„ÅØRLS„Éù„É™„Ç∑„Éº„Åß insert with check (auth.uid() = user_id) „Å®„Åó„Åü„ÅÆ„Åß
                // ÊòéÁ§∫ÁöÑ„Å´ÈÄÅ„Çä„Åæ„Åô„ÄÇ
                user_id: user.id
            }
        ]);

        setIsSubmitting(false);

        if (error) {
            alert(`„Ç®„É©„Éº„ÅåÁô∫Áîü„Åó„Åæ„Åó„Åü: ${error.message}`);
        } else {
            alert('ÊäïÁ®ø„Åó„Åæ„Åó„ÅüÔºÅ');
            setIsModalOpen(false);
            fetchPosts();
        }
    };

    // „É≠„Ç∞„Ç¢„Ç¶„ÉàÂá¶ÁêÜ
    const handleLogout = async () => {
        await supabase.auth.signOut();
        // „É≠„Ç∞„Ç¢„Ç¶„ÉàÂæå„ÅØÂÜçÂ∫¶ÂåøÂêç„É≠„Ç∞„Ç§„É≥„Åï„Åõ„Çã„Åã„ÄÅÁîªÈù¢„É™„É≠„Éº„Éâ
        window.location.reload();
    };

    return (
        <main style={{ width: '100%', height: '100dvh', display: 'flex', flexDirection: 'column', overflow: 'hidden' }}>
            {/* „Éò„ÉÉ„ÉÄ„ÉºÊõ¥Êñ∞Ôºö„É≠„Ç∞„Ç§„É≥„Éú„Çø„É≥ËøΩÂä† */}
            <header
                style={{
                    padding: '10px 15px',
                    background: '#222',
                    color: '#fff',
                    zIndex: 10,
                    display: 'flex',
                    justifyContent: 'space-between',
                    alignItems: 'center',
                    flexShrink: 0
                }}
            >
                <h1 style={{ margin: 0, fontSize: '16px' }}>„Åø„Çì„Å™„ÅÆ„Éè„Ç∂„Éº„Éâ„Éû„ÉÉ„Éó</h1>

                <div>
                    {user && !user.is_anonymous ? (
                        <div style={{ display: 'flex', alignItems: 'center', gap: '10px' }}>
                            <span style={{ fontSize: '12px', color: '#ccc' }}>„É≠„Ç∞„Ç§„É≥‰∏≠</span>
                            <button
                                onClick={handleLogout}
                                style={{
                                    fontSize: '12px',
                                    padding: '4px 8px',
                                    background: '#555',
                                    color: '#fff',
                                    border: 'none',
                                    borderRadius: '4px',
                                    cursor: 'pointer'
                                }}
                            >
                                „É≠„Ç∞„Ç¢„Ç¶„Éà
                            </button>
                        </div>
                    ) : (
                        <button
                            onClick={() => setIsAuthModalOpen(true)}
                            style={{
                                fontSize: '12px',
                                padding: '6px 12px',
                                background: '#0070f3',
                                color: '#fff',
                                border: 'none',
                                borderRadius: '4px',
                                cursor: 'pointer',
                                fontWeight: 'bold'
                            }}
                        >
                            „É≠„Ç∞„Ç§„É≥ / ÁôªÈå≤
                        </button>
                    )}
                </div>
            </header>

            <div style={{ flex: 1, position: 'relative' }}>
                <HazardMap posts={posts} centerPos={center} zoomLevel={zoom} onMapChange={handleMapChange} />

                {/* GPS„Éú„Çø„É≥ */}
                {!isModalOpen && (
                    <button
                        onClick={handleCurrentLocation}
                        disabled={isLoadingGPS}
                        style={{
                            position: 'absolute',
                            bottom: '110px',
                            right: '20px',
                            width: '50px',
                            height: '50px',
                            borderRadius: '50%',
                            background: 'white',
                            color: '#333',
                            fontSize: '24px',
                            border: 'none',
                            boxShadow: '0 4px 10px rgba(0,0,0,0.3)',
                            cursor: 'pointer',
                            zIndex: 1001,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                        }}
                    >
                        {isLoadingGPS ? '...' : 'üìç'}
                    </button>
                )}

                {/* ÊäïÁ®ø„Éú„Çø„É≥ */}
                {!isModalOpen && (
                    <button
                        onClick={() => setIsModalOpen(true)}
                        style={{
                            position: 'absolute',
                            bottom: '40px',
                            right: '20px',
                            width: '60px',
                            height: '60px',
                            borderRadius: '50%',
                            background: '#ff4d4f',
                            color: 'white',
                            fontSize: '30px',
                            border: 'none',
                            boxShadow: '0 4px 10px rgba(0,0,0,0.3)',
                            cursor: 'pointer',
                            zIndex: 1001,
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center'
                        }}
                    >
                        Ôºã
                    </button>
                )}
            </div>

            {/* ÊäïÁ®ø„É¢„Éº„ÉÄ„É´ */}
            {isModalOpen && (
                <div
                    style={{
                        position: 'fixed',
                        top: 0,
                        left: 0,
                        right: 0,
                        bottom: 0,
                        background: 'rgba(0,0,0,0.6)',
                        zIndex: 2000,
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'center'
                    }}
                >
                    <div
                        style={{
                            background: 'white',
                            padding: '20px',
                            borderRadius: '10px',
                            width: '90%',
                            maxWidth: '400px'
                        }}
                    >
                        <h2 style={{ marginTop: 0 }}>‰∏çÂÆâ„ÇíÊäïÁ®ø„Åô„Çã</h2>

                        {/* „É¶„Éº„Ç∂„ÉºÁä∂ÊÖã„Å´„Çà„Çã„É°„ÉÉ„Çª„Éº„Ç∏Âá∫„ÅóÂàÜ„Åë */}
                        {user?.is_anonymous ? (
                            <p style={{ fontSize: '12px', color: '#d32f2f', background: '#ffebee', padding: '8px', borderRadius: '4px' }}>
                                ‚ÄªÁèæÂú®„ÅØ„Äå„Ç≤„Çπ„ÉàÔºàÂåøÂêçÔºâ„Äç„Å®„Åó„Å¶ÊäïÁ®ø„Åó„Åæ„Åô„ÄÇ
                                <br />
                                Âæå„Åß„É≠„Ç∞„Ç§„É≥„Åô„Çã„Å®„ÄÅÂ±•Ê≠¥„Å®„Åó„Å¶‰øùÂ≠ò„Åß„Åç„Åæ„Åô„ÄÇ
                            </p>
                        ) : (
                            <p style={{ fontSize: '12px', color: '#2e7d32', background: '#e8f5e9', padding: '8px', borderRadius: '4px' }}>
                                ‚Äª„É≠„Ç∞„Ç§„É≥Ê∏à„Åø„Ç¢„Ç´„Ç¶„É≥„Éà„ÅßÊäïÁ®ø„Åó„Åæ„Åô„ÄÇ
                            </p>
                        )}

                        <form onSubmit={handlePost}>
                            <div style={{ marginBottom: '15px' }}>
                                <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>ÁêÜÁî±</label>
                                <select value={formReason} onChange={(e) => setFormReason(e.target.value)} style={{ width: '100%', padding: '8px' }}>
                                    <option value="Êöó„Åè„Å¶ÊÄñ„ÅÑ">Êöó„Åè„Å¶ÊÄñ„ÅÑ</option>
                                    <option value="Ë¶ãÈÄö„Åó„ÅåÊÇ™„ÅÑ">Ë¶ãÈÄö„Åó„ÅåÊÇ™„ÅÑ</option>
                                    <option value="‰∫∫ÈÄö„Çä„ÅåÂ∞ë„Å™„ÅÑ">‰∫∫ÈÄö„Çä„ÅåÂ∞ë„Å™„ÅÑ</option>
                                    <option value="Ê≠©ÈÅì„ÅåÁã≠„ÅÑ">Ê≠©ÈÅì„ÅåÁã≠„ÅÑ</option>
                                    <option value="„Åù„ÅÆ‰ªñ">„Åù„ÅÆ‰ªñ</option>
                                </select>
                            </div>

                            <div style={{ marginBottom: '15px' }}>
                                <label style={{ display: 'block', marginBottom: '5px', fontWeight: 'bold' }}>ÊôÇÈñìÂ∏Ø</label>
                                <div style={{ display: 'flex', gap: '10px' }}>
                                    <label>
                                        <input
                                            type="radio"
                                            name="time"
                                            value="day"
                                            checked={formTime === 'day'}
                                            onChange={(e) => setFormTime(e.target.value)}
                                        />{' '}
                                        Êòº
                                    </label>
                                    <label>
                                        <input
                                            type="radio"
                                            name="time"
                                            value="evening"
                                            checked={formTime === 'evening'}
                                            onChange={(e) => setFormTime(e.target.value)}
                                        />{' '}
                                        Â§ïÊñπ
                                    </label>
                                    <label>
                                        <input
                                            type="radio"
                                            name="time"
                                            value="night"
                                            checked={formTime === 'night'}
                                            onChange={(e) => setFormTime(e.target.value)}
                                        />{' '}
                                        Â§ú
                                    </label>
                                </div>
                            </div>

                            <div style={{ display: 'flex', gap: '10px', marginTop: '20px' }}>
                                <button
                                    type="button"
                                    onClick={() => setIsModalOpen(false)}
                                    style={{ flex: 1, padding: '10px', background: '#ccc', border: 'none', borderRadius: '4px', cursor: 'pointer' }}
                                >
                                    „Ç≠„É£„É≥„Çª„É´
                                </button>
                                <button
                                    type="submit"
                                    disabled={isSubmitting}
                                    style={{
                                        flex: 1,
                                        padding: '10px',
                                        background: '#ff4d4f',
                                        color: 'white',
                                        border: 'none',
                                        borderRadius: '4px',
                                        cursor: 'pointer'
                                    }}
                                >
                                    {isSubmitting ? 'ÈÄÅ‰ø°‰∏≠...' : 'ÊäïÁ®ø„Åô„Çã'}
                                </button>
                            </div>
                        </form>
                    </div>
                </div>
            )}

            {/* „É≠„Ç∞„Ç§„É≥„É¢„Éº„ÉÄ„É´ */}
            <AuthModal isOpen={isAuthModalOpen} onClose={() => setIsAuthModalOpen(false)} />
        </main>
    );
}
